import React, { useRef } from "react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import {SKILLS} from './data';
import  Slider  from 'react-slick';
export const Skills = () => {
  
    const sliderRef = useRef();
    const settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 2,
        slidesToScroll: 2,
        arrows: false,
        responsive:[
            {
                breakpoint: 992,
                settings:{
                    slidesToShow: 2,
                    slidesToScroll: 2,
                },
            },
            {
                breakpoint: 767,
                settings:{
                    slidesToShow: 1,
                    slidesToScroll: 1,
                },
            },
        ],
    };
    const slideRight = () => {
        sliderRef.current.slickNext();
    }
    const slideLeft = () => {
        sliderRef.current.slickPrev();
    }
    return(
        <section className="experience-container skill-bx" id="skills">
            <h5>Skills</h5>
            <div className="experience-content">
                <div className="arrow-right1" onClick={slideRight} >
                    <span className="material-symbols-outlined">&#8250;</span>
                </div>
                <div className="arrow-left1" onClick={slideLeft} >
                    <span className="material-symbols-outlined">&#8249;</span>
                </div>
                <Slider className="skill-card" ref={sliderRef}  {...settings}>
                    {SKILLS.map((skill, index) => (
                          <div key={index} className="item">
                            <p className="tagline">{2 * index + 1 }. {skill.first}</p> 
                            <p className="tagline">{2 * index + 2 }. {skill.second}</p> 
                          </div>
                        ))}
                </Slider>
            </div>
        </section>
    )
}
