import React, { useRef } from "react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { WorkExperienceCard } from './WorkExperienceCard';
import colorSharp from '../assets/img/color-sharp.png';
import {WORK_EXPERIENCE} from './data';
import  Slider  from 'react-slick';
export const WorkExperience = () => {
    const sliderRef = useRef();
    const settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false,
        responsive:[
            {
                breakpoint: 767,
                settings:{
                    slidesToShow: 1,
                    slidesToScroll: 1,
                },
            },
        ],
    };
    const slideRight = () => {
        sliderRef.current.slickNext();
    }
    const slideLeft = () => {
        sliderRef.current.slickPrev();
    }
    return(
        <section className="experience-container skill-bx" id="experiences">
            <h5>Work Experience</h5>
            <div className="experience-content">
                <div className="arrow-right1" onClick={slideRight} >
                    <span className="material-symbols-outlined">&#8250;</span>
                </div>
                <div className="arrow-left1" onClick={slideLeft} >
                    <span className="material-symbols-outlined">&#8249;</span>
                </div>
                <Slider ref={sliderRef}  {...settings}>
                    {WORK_EXPERIENCE.map((item)=>(
                    <WorkExperienceCard key={item.title} details={item} />
                    ))}
                </Slider>
            </div>
        </section>
    )
}
