import React from "react";
import navIcon1 from "../assets/img/nav-icon1.svg";
import navIcon2 from "../assets/img/nav-icon2.svg";

export const Card = ({details}) => {
    return(
        <div className="project-card">
            <h6>{details.title}</h6>
            <img className="project-duration" src={details.url} alt="" />
            <ul>
                {details.responsibilities.map((item) => (
                    <li key={item}>{item}</li>
                ))}
            </ul>
            <div className="social-icon project-box">
            {/* <a href=""><img className="icon1" src={navIcon1} alt="First Icon" /></a> */}
            <hr />
            <a href={details.GitUrl}><img className="icon1" src={navIcon2} alt="Second Icon" /></a>
            </div>
        </div>
    )
}