import { useState } from "react";
import { Col } from "react-bootstrap";
import imgUrl from '../assets/img/emailIcon.svg';
import { ContactInfoCard } from "./ContactInfoCard";

export const Contact = () => {
    const formInitialDetails = {
        firstName: '',
        lastName: '',
        email: '',
        phone: '',
        message: ''
    }
    const [formDetails, setFormDetails] = useState(formInitialDetails);
    const [buttonText, setButtonText] = useState('Send');

    const onFormUpdate = (category, value) => {
        setFormDetails({
            ...formDetails,
            [category]: value
        })
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        setButtonText("Sending...");
        let response = await fetch("https://react-firebase-14074-default-rtdb.firebaseio.com/UserData.json", {
        // let response = await fetch("http://localhost:3000/contact", {
            method: "POST",
            headers: {
                "Content-Type": "application/json;charset=utf-8",
            },
            body: JSON.stringify(formDetails),
        });
        console.log(formDetails);
        setFormDetails(formInitialDetails);
        setButtonText("Send");
        if (response.ok) {
            console.log('Form submitted successfully');
        } else {
            console.error('Failed to submit form');
          }
    };
    return (
        <section className="skill-bx" id="contact" style={{ margin: '2rem 1rem' }}>
            <div className="contact-container">
                <h5>Get In Touch</h5>
                <div className="contact-content">
                    <div style={{ flex: 1 }}>
                        <div className="email-content">
                            <h6>OR</h6>
                            <h6>Drop Email for Collaboration</h6>
                        </div>
                        <ContactInfoCard
                            iconUrl={imgUrl}
                            text="alishbahshareef@gmail.com"
                        />
                    </div>
                    <div style={{ flex: 1 }}>
                        <div className="contact-form-content">
                            <form onSubmit={handleSubmit}>
                                <input type="text" name="firstname" value={formDetails.firstName} placeholder="First Name" onChange={(e) => onFormUpdate('firstName', e.target.value)} required/>
                                <input type="text" name="lastname" value={formDetails.lastName} placeholder="Last Name" onChange={(e) => onFormUpdate('lastName', e.target.value)} required/>
                                <input type="email" name="email" value={formDetails.email} placeholder="Email Address" onChange={(e) => onFormUpdate('email', e.target.value)} required/>
                                <input type="tel" name="phone" value={formDetails.phone} placeholder="Phone No." onChange={(e) => onFormUpdate('phone', e.target.value)} required/>
                                <textarea rows="6" name="message" value={formDetails.message} placeholder="Message" onChange={(e) => onFormUpdate('message', e.target.value)} required></textarea>
                                <button type="submit" style={{ width: '100%' }}><span>{buttonText}</span></button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}
