import React, { useState, useEffect } from 'react';
import { Navbar, Nav, Container, Offcanvas } from 'react-bootstrap';
import logo from '../assets/img/Logo.png';
import { BrowserRouter as Router } from 'react-router-dom';

export const NavBar = () => {
  const [activeLink, setActiveLink] = useState('home');
  const [scrolled, setScrolled] = useState(false);
  const [showMenu, setShowMenu] = useState(false);

  useEffect(() => {
    const onScroll = () => {
      if (window.scrollY > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };

    window.addEventListener('scroll', onScroll);
    return () => window.removeEventListener('scroll', onScroll);
  }, []);

  const onUpdateActiveLink = (value) => {
    setActiveLink(value);
    setShowMenu(false); // Close the menu when a link is clicked
  };

  const handleToggleMenu = () => {
    setShowMenu(!showMenu);
  };

  return (
    <Router>
      <Navbar expand="md" className={scrolled ? 'scrolled' : ''}>
        <Container>
          <Navbar.Brand href="/">
            <img src={logo} alt="Logo" />
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" onClick={handleToggleMenu} />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ms-auto">
              <Nav.Link href="#home" className={activeLink === 'home' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('home')}>
                Home
              </Nav.Link>
              <Nav.Link href="#skills" className={activeLink === 'skills' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('skills')}>
                Skills
              </Nav.Link>
              <Nav.Link href="#projects" className={activeLink === 'projects' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('projects')}>
                Projects
              </Nav.Link>
              <Nav.Link href="#experiences" className={activeLink === 'experiences' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('experiences')}>
                Experience
              </Nav.Link>
              <Nav.Link href="#contact" className={activeLink === 'contact' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('contact')}>
                Contact me
              </Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>

        <Offcanvas show={showMenu} onHide={() => setShowMenu(false)} placement="end">
            <br></br>
            <br></br>
            <br></br>
          <Offcanvas.Body>
            <Nav className="d-flex flex-column">
              <Nav.Link href="#home" onClick={() => onUpdateActiveLink('home')}>
                Home
              </Nav.Link>
              <Nav.Link href="#skills" onClick={() => onUpdateActiveLink('skills')}>
                Skills
              </Nav.Link>
              <Nav.Link href="#projects" onClick={() => onUpdateActiveLink('projects')}>
                Projects
              </Nav.Link>
              <Nav.Link href="#experiences" onClick={() => onUpdateActiveLink('experiences')}>
                Experience
              </Nav.Link>
              <Nav.Link href="#contact" onClick={() => onUpdateActiveLink('contact')}>
                Contact me
              </Nav.Link>
            </Nav>
          </Offcanvas.Body>
        </Offcanvas>
      </Navbar>
    </Router>
  );
};
